import React from 'react'

export class Profile extends React.Component{
    render(){
        var containerStyle={
            position:'relative',
            
            display: 'contents',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            minHeight:'150px'
        };
        var imageStyle={
            margin:'20px 5px 50px 20px',
            width: '300px',            
            overflow: "hidden",    
            float:'left',
            
        };
        var articleStyle={
        borderRight:"1px solid #000",
        borderLeft:"1px solid #000",
        wordWrap: "break-word",
        width: "400px",
        margin:'10px 10px 5px 10px',
        padding: '0px 5px 0px 5px',
        float:'left',
        color: '#222',
        fontFamily: 'Open Sans sansSerif',
        fontSize: '15px',
        fontWeight: '400',
        lineHeight: '24px'
        };
        var logoStyle={
            position:'absolute',
            bottom:'5px',
            left:'5px',
            width:'500px',
            margin:'auto'
        };
        return (
            <span className="Profile-container" style={containerStyle}>
                
                <img src={this.props.url} style={imageStyle} alt='profile-img' border='0'></img>
                
                <article style={articleStyle}>Hey! I'm {this.props.name}
                 <br></br>
                 {this.props.text}
                </article>
                <img src={this.props.foot} style={logoStyle} alt='logo-img' border='0'></img>
                
            </span>
            
        );
    }
}

export default Profile